@import '../../sass/variables.scss';

.main,
.itemPage {
  width: 100%;
}

.main {
  flex-grow: 1;
}

.container {
  width: $PC-container-width;

  margin: 0 auto;
  padding: 0 20px;
  padding-top: 40px;
}

.shopName {
  color: $menu-hover-color;
}

.link {
  margin-left: 5px;
  color: $second-text-color;
}

.link:hover,
.link:focus {
  color: $menu-hover-color;
}

.wrapper {
  max-width: 500px;
  margin: 0 auto;
  padding: 10px;

  border: 2px solid $menu-bar-color;
  border-radius: 10px 10px;
}

.services {
  margin-top: 20px;
}

@media (max-width: $PC-max-width) {
  .container {
    width: $MPC-container-width;
  }
}

@media (max-width: $MPC-max-width) {
  .main {
    padding-top: 60px;
  }

  .container {
    box-sizing: border-box;
    width: 100%;
  }
}

@media (max-width: $Tablet9-max-width) {
  .container {
    padding-top: 40px;
  }
}
