/*Open Sans Regular*/
@font-face {
  font-family: 'Open Sans';
  src: url("./fonts/opensans.woff2") format("woff2"), url("./fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

/*Open Sans Light*/
@font-face {
  font-family: 'Open Sans';
  src: url("./fonts/opensanslight.woff2") format("woff2"), url("./fonts/opensanslight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

/*Open Sans Bold*/
@font-face {
  font-family: 'Open Sans';
  src: url("./fonts/opensansbold.woff2") format("woff2"), url("./fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
legend,
fieldset,
blockquote,
body {
  margin: 0px;
  padding: 0px; }

body {
  min-width: 320px;
  background-color: #f7f3ed;
  font-family: 'Open Sans', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: black; }

input,
textarea {
  font-family: 'Open Sans', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: #283645; }

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

@media (max-width: 992px) {
  body {
    font-size: 16px;
    line-height: 20px; } }

@media (max-width: 480px) {
  body {
    font-size: 14px;
    line-height: 18px; } }

@media (max-width: 320px) {
  body {
    font-size: 12px;
    line-height: 16px; } }
