@import '../../../sass/variables.scss';

.button {
  display: inline;
  padding: 0;

  background-color: transparent;
  border: none;

  text-decoration: underline;

  color: $second-text-color;
  @include text-button;
}

@media (max-width: 400px) {
  .button {
    font-size: 16px;
  }
}
